import React from "react";
import "./App.css";
import Layout from "./components/Layout";
import Testpage from "./containers/Testpage";
import Test from "./containers/Test";
import Homepage from "./containers/homepage";

import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import YouthAdvocates from "./containers/YouthAdvocates";
import PeerCounsellor from "./containers/PeerCounsellor";
import Profile from "./containers/Profile";
import Questions from "./containers/Questions";
import Result from "./containers/Result";
import FreeCounseling from "./containers/FreeCounseling";
import Advocates from "./containers/Advocates";
import College from "./containers/college";
import Blogs from "./containers/Blogs";
import Blog from "./containers/Blog";
import Blogger from "./containers/Blogger";
import withTracker from "./withTracker";

function App() {
  return (
    <div className="App">
      <Router>
        <Layout>
          <Switch>
            <Route path="/test/:testName" component={withTracker(Test)} />
            <Route path="/test">
              <Testpage />
            </Route>
            <Route
              path="/freecounseling"
              component={withTracker(PeerCounsellor)}
            />
            <Route
              path="/freecounselingform"
              component={withTracker(FreeCounseling)}
            />
            <Route
              path="/youthAdvocates"
              component={withTracker(YouthAdvocates)}
            />
            <Route path="/profile/:name">
              <Profile />
            </Route>
            <Route path="/questions/:testname">
              <Questions />
            </Route>
            <Route path="/result/:testname/:score/:tips">
              <Result />
            </Route>
            <Route path="/youthadvocate/:name">
              <Advocates />
            </Route>
            <Route path="/blogs">
              <Blogs />
            </Route>
            <Route path="/blog/:name">
              <Blog />
            </Route>
            <Route path="/blogger/:name">
              <Blogger />
            </Route>
            <Route path="/:name">
              <College />
            </Route>
            <Route path="/" component={withTracker(Homepage)} />
          </Switch>
        </Layout>
      </Router>
    </div>
  );
}

export default App;
